<template>
    <div id="release-order-page">
        <div class="line">
            <h2></h2>
        </div>
        <div class="line input-box">
            <span></span>
            <el-input v-model="defaultData.lessor_name" placeholder="" disabled></el-input>
        </div>
        <div class="line input-box">
            <span></span>

            <el-input v-model="defaultData.lessor_id_card" placeholder="" disabled></el-input>
        </div>
        <div class="line input-box">
            <span></span>
            <el-input v-model="defaultData.tenant_name" placeholder="" disabled></el-input>
        </div>
        <div class="line input-box">
            <span></span>
            <el-input v-model="defaultData.tenant_id_card" placeholder="" disabled></el-input>
        </div>
        <div class="line">
            <span></span>
            <el-select v-model="defaultData.acc_type" placeholder="" disabled>
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="line input-box">
            <span> </span>
            <el-input v-model="defaultData.acc_capacity" placeholder="" disabled></el-input>
        </div>
        <div class="line input-box address">
            <span> </span>
            <el-input v-model="defaultData.acc_address" placeholder="" disabled></el-input>
        </div>
        <div class="line">
            <span></span>
            <el-date-picker v-model="dateRange" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="getPrice()">
            </el-date-picker>
        </div>
        <div class="line input-box">
            <span></span>
            <el-input v-model="price" placeholder=""></el-input>
        </div>
        <div class="line text-box">
            
            <br>
            <br>
            <el-input v-model="demand" type="textarea" placeholder=""></el-input>
        </div>
        <div class="button-container">
            <button class="common-button" @click="commit()"></button>
        </div>
    </div>
</template>


<script>
const ReleaseOrderPage = {
    data: function() {
        return {
            type: 0,
            options: [
                { value: 1, label: "" },
                { value: 2, label: "" },
                { value: 3, label: "" },
                { value: 4, label: "" },
                { value: 5, label: "" }
            ],
            dateRange: [],

            defaultData: {},
            price: "",
            demand: ""
        };
    },
    methods: {
        getOrderInformation: function() {
            const self = this;

            var postData = {
                acc_id: this.$route.params.buildingId
            };

            this.$axios
                .post("/c2c/reservation/request", postData)
                .then(function(response) {
                    var data = response.data;

                    if (data.success) {
                        self.defaultData = data;
                    }
                });
        },

        getPrice: function() {
            var time =
                (Date.parse(this.dateRange[1]) -
                    Date.parse(this.dateRange[0])) /
                86400000;
            this.price = time * parseInt(this.defaultData.price);
        },

        commit: function() {
            const self = this;

            var postData = {
                acc_id: this.$route.params.buildingId,
                price: this.price,
                begin: Date.parse(this.dateRange[0]),
                end: Date.parse(this.dateRange[1]),
                demand: this.demand
            };

            this.$axios
                .post("/c2c/reservation/add", postData)
                .then(function(response) {
                    var data = response.data;

                    if (data.success) {
                        self.$message({
                            message: "",
                            type: "success"
                        });
                        self.$router.push(
                            "/PersonalCenterPage/ViewAllOrdersPage"
                        );
                    }
                });
        }
    },
    created() {
        this.getOrderInformation();
    }
};

export default ReleaseOrderPage;
</script>


<style lang="scss">
#release-order-page {
    padding: 30px;
    text-align: left;
    margin-bottom: 50px;

    .line {
        padding: 20px 0 20px;
        border-bottom: 1px solid #bbb;

        span {
            display: inline-block;
            width: 120px;
        }
    }

    .input-box {
        .el-input {
            width: 300px;
        }

        input {
            width: 220px;
            height: 40px;
        }
    }

    .address {
        .el-input {
            width: 600px;
        }

        input {
            width: 400px;
            height: 40px;
        }
    }

    .text-box {
        .el-input {
            width: 300px;
        }

        textarea {
            width: 740px;
            height: 100px;
        }
    }

    .button-container {
        text-align: center;

        .common-button {
            margin-top: 35px;
            width: 150px;
            height: 40px;
            font-size: 16px;
        }
    }
}
</style>
