<template>
    <div id="house-detail-page">
        <div class="header">
            <div class="container clearfix">
                <div class="left-bar">
                    <span>
                        <a href=""></a>
                    </span>
                    <span>
                        <a href=""></a>
                    </span>
                    <span>
                        <a href=""></a>
                    </span>
                </div>
                <div class="right-bar">
                    <span v-if="userId">
                        <router-link to="/PersonalCenterPage"></router-link>
                    </span>
                    <span v-if="userId">
                        <a href="" @click.prevent="logout()"></a>
                    </span>
                    <span v-if="!userId">
                        <a href="" @click.prevent="registToggle()"></a>
                    </span>
                    <span v-if="!userId">
                        <a href="" @click.prevent="loginToggle()"></a>
                    </span>
                    <span>
                        <a href=""></a>
                    </span>
                </div>
            </div>
        </div>
        <div class="img-show">
            <el-carousel trigger="hover" height="500px">
                <el-carousel-item v-for="(item,index) in defaultData.acc_images" :key="index">
                    <img :src="item" alt="">
                </el-carousel-item>
            </el-carousel>
        </div>
        <div class="content">
            <div class="line">
                <span class="remark">&nbsp;&nbsp;&nbsp;&nbsp;  </span>
                <h1>{{defaultData.acc_address}}</h1>
            </div>
            <div class="line">
                <h3></h3>
                <span>{{defaultData.acc_type_name}}</span>
                <span>{{defaultData.acc_capacity}}</span>
                <span>{{defaultData.acc_price}}</span>
                <br>
                <br>
            </div>
            <div class="line">
                <h3></h3>
                <p>
                    {{defaultData.acc_description}}
                </p>
                <!-- <p>DUDUSA</p>
                <p>1. ,,,(,) </p>
                <p>2. ,,,4k,!,,,,,</p>
                <p>3. ,,24,,Wi-Fi,,,</p>
                <p>4. ,!</p>
                <p>5. ,,,,,</p>
                <p>6. ,,,,5IFS,,5,!10,!,,3,5</p> -->
            </div>
            <div class="order-container">
                <div class="lessor-box">
                    <img :src="defaultData.headico" class="head-img">
                    <span class="name">{{defaultData.nickname}}</span>
                    &nbsp;&nbsp;&nbsp;
                    <span class="phone">
                        <a href=""> &nbsp;{{defaultData.phone}}</a>
                    </span>
                    <br>
                    <br>
                    <span class="comment">
                        {{defaultData.comment_count }}  
                    </span>
                </div>
                <button class="order" @click.prevent="order()">
                    <a href="javascript:;"></a>
                </button>
            </div>
            <div class="comment-box clearfix">
                <div class="line">
                    <h2></h2>
                    <span>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <span class="remark">{{defaultData.comment_count }}</span>
                    </span>
                </div>
                <div class="comment-line line" v-for="(comment, index) in commentList" :key="index">
                    <img :src="comment.headico" class="head-img">
                    <span class="name">{{comment.nickname}}</span>
                    &nbsp;&nbsp;&nbsp;
                    <br>
                    <br>
                    <span>{{comment.date}}</span>
                    <br>
                    <br>
                    <p>{{comment.content}}</p>
                </div>
                <!-- <div class="comment-line line">
                    <img src="../assets/20.jpg" class="head-img">
                    <span class="name"></span>
                    &nbsp;&nbsp;&nbsp;
                    <br>
                    <br>
                    <span>20186</span>
                    <br>
                    <br>
                    <p>500510</p>
                </div>
                <div class="comment-line line">
                    <img src="../assets/21.jpg" class="head-img">
                    <span class="name"></span>
                    &nbsp;&nbsp;&nbsp;
                    <br>
                    <br>
                    <span>20186</span>
                    <br>
                    <br>
                    <p>         nice </p>
                </div> -->
                <div class="load" @click="getCommentList()" v-show="!isEnd">
                    
                </div>
            </div>
        </div>
        <transition name="fade">
            <regist-box v-show="isRegistShow" v-on:callback="registToggle()"></regist-box>
        </transition>
        <transition name="fade">
            <login-box v-show="isLoginShow" v-on:callback="loginToggle()"></login-box>
        </transition>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import LoginBox from "../components/LoginBox";
import RegistBox from "../components/RegistBox";

const HouseDetailPage = {
    data: function() {
        return {
            isRegistShow: false,
            isLoginShow: false,

            defaultData: {},
            start: 0,
            isEnd: false,
            commentList: []
        };
    },
    components: {
        LoginBox: LoginBox,
        RegistBox: RegistBox
    },
    computed: {
        ...mapGetters(["userId", "roleId"])
    },
    methods: {
        registToggle: function() {
            this.isRegistShow = !this.isRegistShow;
        },

        loginToggle: function() {
            this.isLoginShow = !this.isLoginShow;
        },

        logout: function() {
            const self = this;

            this.$axios.post("/c2c/logout").then(function(response) {
                var data = response.data;

                if (data.success) {
                    // logout success
                    self.$cookie.deleteAllCookies();
                    self.$router.push("/");
                }
            });
        },

        order: function() {
            if (!this.userId) {
                this.$message.error("");
            } else if (this.roleId != 4) {
                this.$message.error("");
            } else {
                this.$router.push(
                    "/PersonalCenterPage/ReleaseOrderPage/" +
                        this.$route.params.buildingId
                );
            }
        },

        getBuildingInformation: function() {
            const self = this;

            var postData = {
                acc_id: this.$route.params.buildingId
            };

            this.$axios
                .post("/c2c/accommodation/show", postData)
                .then(function(response) {
                    var data = response.data;

                    if (data.success) {
                        self.defaultData = data;
                    }
                });
        },

        getCommentList: function() {
            const self = this;

            var postData = {
                index: this.start,
                count: 5,
                acc_id: this.$route.params.buildingId
            };

            this.$axios
                .post("/c2c/comment/list", postData)
                .then(function(response) {
                    var data = response.data;

                    if (data.success) {
                        self.commentList = self.commentList.concat(
                            data.comments
                        );
                        self.start += 5;

                        if (data.empty) {
                            self.isEnd = true;
                        }
                    }
                });
        }
    },
    created() {
        this.getBuildingInformation();
        this.getCommentList();
    }
};

export default HouseDetailPage;
</script>



<style lang="scss" scoped>
#house-detail-page {
    width: 100%;
    padding-bottom: 50px;

    .header {
        width: 100%;
        height: 40px;

        .container {
            margin: 0 auto;
            width: 1300px;
            color: #000;
            font-size: 16px;
            line-height: 40px;

            .city {
                margin: 0;
            }

            span {
                margin-left: 20px;
            }

            a {
                color: #000;
            }

            a:hover {
                color: #ff5a5f;
            }

            .left-bar {
                float: left;
            }

            .right-bar {
                float: right;
            }
        }
    }

    .img-show {
        margin: 0 auto;
        margin-top: 15px;
        width: 1300px;

        img {
            width: 1300px;
            height: 500px;
        }
    }

    .content {
        margin: 0 auto;
        width: 1300px;
        text-align: left;
        margin-top: 10px;

        .line {
            border-bottom: 1px solid #bbb;

            h1 {
                margin: 20px 0;
                margin-top: 10px;
            }

            .remark {
                font-size: 14px;
            }
        }

        .order-container {
            position: relative;
            margin-left: 80px;
            margin-top: 20px;

            .head-img {
                position: absolute;
                margin-left: -80px;
                margin-top: -5px;
                border-radius: 35px;
                width: 70px;
                height: 70px;
            }

            .name {
                font-weight: bold;
            }

            .phone {
                font-weight: bold;

                a {
                    color: #008489;
                }
            }

            .comment {
                font-size: 13px;
            }

            .order {
                position: absolute;
                top: 15px;
                right: 0;
                border: none;
                border-radius: 5px;
                width: 200px;
                height: 50px;
                background: #ff5a5f;
                color: #fff;

                a {
                    color: #fff;
                }
            }
        }

        .comment-box {
            margin-top: 70px;

            .el-icon-star-on {
                color: #00847f;
            }

            .comment-line {
                min-height: 100px;
                margin-top: 40px;
                padding-left: 70px;

                .head-img {
                    position: absolute;
                    margin-left: -80px;
                    margin-top: -5px;
                    border-radius: 35px;
                    width: 70px;
                    height: 70px;
                }

                .name {
                    font-weight: bold;
                }
            }

            .load {
                margin-top: 50px;
                margin-bottom: 50px;
                text-align: center;
                font-style: italic;
                font-size: 20.9984px;
                color: #c5c5c5;
                cursor: pointer;
            }
        }
    }
}
</style>

